  border-radius: 2px;
  cursor: pointer;
  justify-content: center;
  text-align: center;
  letter-spacing: inherit;
  white-space: nowrap;
  transition: background .15s ease; }
  .button:active {
    outline: 0; }
  .button:hover {
    background-color: #dfe1e1; }
  .button.is-loading {
    position: relative;
    color: transparent !important;
    pointer-events: none; }
    .button.is-loading::after {
      content: '';
