      border-radius: 50%;
      border-right-color: transparent !important;
      border-top-color: transparent !important;
      z-index: 1;
      animation: button-loading .6s infinite linear; }
  .button[disabled] {
    cursor: not-allowed;
    color: #33363A;
    background-color: #717D86 !important; }
    .button[disabled].is-loading::after {
      border-color: #33363A; }

